.mainContent {
    min-width: 600px;
    position: fixed;
    top: 25%;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: rgba(255, 255, 255, 0.2);
}

@keyframes leftWave {
  0% { 
    left: -80px; 
  }
  100% { 
    left: 0; 
  }
}

@keyframes rightWave {
  0% { 
    left: 0px; 
  }
  100% { 
    left: -80px; 
  }
}

.leftContainer {
    width: 100px;
    overflow: hidden;
    position: fixed;
    left: 120px;
    top: 405px;
}

.previousArrow {
    position: fixed;
    left: 100px;
    height: 32px;
    top: 400px;
    transform: rotate(270deg);
}

.prevArrowWrapper {
    cursor: pointer;
}

.nextArrowWrapper {
    cursor: pointer;
}

.nextArrow {
    position: fixed;
    right: 100px;
    top: 400px;
    height: 32px;
    transform: rotate(90deg);
}

.rightContainer {
    width: 100px;
    overflow: hidden;
    position: fixed;
    right: 120px;
    top: 405px;
}

.leftContainer svg {
  position: relative;
  left: -50px;
  width: 200px;
  animation: leftWave 2s linear infinite;
}

.rightContainer svg {
  position: relative;
  right: -50px;
  width: 200px;
  animation: rightWave 2s linear infinite;
}

.imgWrapper {
    background-color: white;
    color: blue;
    border: solid 5px blue;
    max-width: 800px;
    padding: 20px;
}

.image {
    max-width: 100%;
}

.contentBody {
    margin-bottom: 30px;
}

.st69{
    fill:none;
    stroke: white;
    stroke-width:12;
    stroke-miterlimit:10;
}

.contentTitle {
    text-align: center;
}

.contentProject {
    font-weight: bold;
}

.linkToProject {
    color: blue;
    margin-top: 30px;
    display: block;
}

.press {
    font-weight: bold;
    display: inline;
}

.pressLink {
    color: blue;
    margin-left: 5px;
    margin-right: 5px;
}

.info {
    color: white;
    cursor: pointer;
    position: absolute;
    bottom: 32px;
    text-decoration: underline;
    font-weight: bold;
    font-size: 48px;
    right: 30px;
    height: 40px;
    animation: rotate 3s infinite linear;
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}

@media only screen and (max-width: 1000px) {
    .mainContent {
        position: fixed;
        width: 100%;
        min-width: 100%;
        left: 0;
        top: 270px;
        transform: none;
    }

    .image {
        max-width: 330px;
    }

    .previousArrow {
        left: 5px;
        top: 205px;
    }

    .leftContainer {
        left: 34px;
        top: 213px;
    }

    .nextArrow {
        right: 5px;
        top: 550px;
    }

    .rightContainer {
        right: 34px;
        top: 558px;
    }

    .imgWrapper {
        text-align: center;
        max-height: 200px;
        overflow-y: scroll;
        overflow-x: hidden;
    }

    .info {
        bottom: 260px;
    }
}